import React from "react";
import { useNavigate } from "react-router";
import { getCount } from "../../api/util";
import { ListWraper, List, ListItem } from './style';
import LazyLoad from "react-lazyload";
// import { Button } from "antd";
// import { EllipsisOutlined } from "@ant-design/icons";

function ComicList(props) {
    const { recommendList, currentTag } = props;
    const history = useNavigate();
    const enterDetails = (id) => {
        history(`/detail/${id}`)
    }
    return (
        <ListWraper>
            {/* {
                (recommendList.length >= 12) ? (
                    <Button icon={<EllipsisOutlined />} style={{ background: '#999', marginLeft: 910, marginTop: 8 }}></Button>
                ) : null
            } */}
            <List>
                {
                    recommendList.map((item, index) => {
                        return (
                            <ListItem key={item.comicId} onClick={() => enterDetails(item.comicId)}>
                                <div className="img_wrapper">
                                    <LazyLoad placeholder={<img width="100%" height="100%" src={require('../../assets/load.webp')} />} >
                                        <img src={item.cover} width='100%' height='100%' alt={`${currentTag}`} />
                                    </LazyLoad>
                                </div>
                                <div className="desc">{item.title}</div>
                            </ListItem>
                        )
                    })
                }
            </List>
        </ListWraper>
    )
}
export default ComicList;