import React from "react";
import { useSelector } from "react-redux";
import { Outlet } from "react-router-dom";
import { NavLink } from "react-router-dom";
import { Head, Tab } from './style';
import { Breadcrumb, Layout, Menu } from 'antd';
import TagList from "../../component/tag";

const { Header, Content, Footer } = Layout;
const item = [
    {
        label: (
            <NavLink to='/' className={'link'}></NavLink>
        ),
        key: 'home',
    },
    {
        label: (
            <NavLink to='/comicSearch' className={'link'}></NavLink>
        ),
        key: 'comicSearch'
    },
    {
        label: (
            <NavLink to='/recent' className={'link'}>()</NavLink>
        ),
        key: 'novel',
        disabled: true,
    },
    {
        label: (
            <a href="https://www.bilibili.com/video/BV1gV411i7vq?spm_id_from=333.337.search-card.all.click" className="link"></a>
        ),
        key: 'link'
    }
]

function Home() {
    return (
        <div>

            <Layout>
                <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
                    <Menu
                        theme="dark"
                        mode='horizontal'
                        items={item}
                    />

                </Header>
                <Content style={{ background: '#001529' }}>
                    <div style={{marginTop:80}}>
                        <Outlet />
                    </div>

                </Content>
                <Footer style={{ textAlign: 'center', background: '#001529', color: 'white' }}>
                    Tip:APILRY
                </Footer>
            </Layout>

        </div>

    )
}
export default React.memo(Home);