import React from "react";
import { useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router";
import { ChapterList, ChapterListItem, DetailWrapper, Detail_left, Detail_right } from "./style";
import { getChapterCount, changeHasReadData } from '../../application/ComicDetails/store/actionCreators';
import { Button } from "antd";
function DetailPage(props) {

    const { detailData } = props;
    const history = useNavigate();
    const dispatch = useDispatch();

    const enterContent = (id, item, data) => {
        const currentChapter = detailData.chapterList.indexOf(item);
        console.log(currentChapter)
        dispatch(getChapterCount(currentChapter));
        dispatch(changeHasReadData(data))
        history(`/content/${id}`)
    }
    return (
        <DetailWrapper>
            <Detail_left>
                <h1 className="title">{detailData.title}</h1>
                <span className="describe">:{detailData.descs}</span>
                <span className="chapter"></span>
                <ChapterList>
                    {
                        detailData.chapterList.map((item) => {
                            return (
                                <Button
                                style={{background:"#666",border:0,margin:5}}
                                    key={item.index}
                                    onClick={() => enterContent(item.chapterId, item, [detailData.title, detailData.comicId, detailData.chapterList])}
                                >
                                    <div className="chapterItem">
                                        <span>{item.title}</span>
                                    </div>
                                </Button>
                            )
                        })
                    }
                </ChapterList>
            </Detail_left>

            <Detail_right>
                <div className="name row">
                    <span className="left"></span>
                    <span className="right">{detailData.title}</span>
                </div>
                <div className="image row">
                    <span className="left"></span>
                    <img src={detailData.cover} className="right"></img>
                </div>
                <div className="author row">
                    <span className="left"></span>
                    <span className="right">{detailData.author}</span>
                </div>
                <div className="type row">
                    <span className="left"></span>
                    <span className="right">{detailData.comicType}</span>
                </div>
                <div className="update row">
                    <span className="left"></span>
                    <span className="right">{detailData.updateTime}</span>
                </div>
            </Detail_right>
        </DetailWrapper>
    )
}
export default DetailPage;