import * as actionTypes from './constants';
import { getBannerRequest, getKuaikan, getRecommendListRequest } from '../../../api/request';

export const changeBannerList = (data) => ({
  type: actionTypes.CHANGE_BANNER,
  data
});

export const changeRecommendList = (data) => ({
  type: actionTypes.CHANGE_RECOMMEND_LIST,
  data
});

const changeRecommendListCount = (data)=>({
  type:actionTypes.CHANGE_RECOMMEND_LIST_COUNT,
  data
})

const changeKuaiKanData = (data)=>({
  type:actionTypes.CHANGE_KUAI_KAN_DATA,
  data
})

export const getBannerList = (type) => {
  return (dispatch) => {
    getBannerRequest(type).then(res => {
      const action = changeBannerList(res.data);
      dispatch(action);
    }).catch(() => {
      console.log('');
    })
  }
};
export const getRecommendList = (type,page) => {
  return (dispatch) => {
    getRecommendListRequest(type,page).then(res => {
      dispatch(changeRecommendList(res.data));
      dispatch(changeRecommendListCount(res.count));
    }).catch(() => {
      console.log("");
    });
  }
};
export const getKuaikanbanner = () => {
    return dispatch =>{
     getKuaikan().then(res =>{
       dispatch(changeKuaiKanData(res));
     }).catch(()=>{
       console.log('err')
     })
    }
}