import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Slider from "../../component/slider";
import ComicList from "../../component/list";
import RankList from "../../component/rankList";
import * as actionTypes from './store/actionCreators';
import { HomeContentStyle, Warn, LastWatch } from "./style";
import { isEmptyObject, comicType } from "../../api/util";
import { useNavigate } from "react-router";
import Loading from "../../ui/loading/loading";
import Tags from "../../component/tag";
import {Pagination} from 'antd'


function HomeContent() {
    const dispatch = useDispatch();
    const type1 = comicType[Math.floor(Math.random() * comicType.length)]
    const type2 = comicType[Math.floor(Math.random() * comicType.length)]

    const [tag, setTag] = useState('');
    const [currentPage,setCurrentPage] = useState(1);
    useEffect(() => {
        setCurrentPage(1);
        // if (bannerList !== null) {
        //     dispatch(actionTypes.getBannerList(type1));
        // }
        if (recommendList !== null) {
            dispatch(actionTypes.getRecommendList(tag,currentPage));
        }
        
    }, [tag]);

    useEffect(()=>{
        if (recommendList !== null) {
            dispatch(actionTypes.getRecommendList(tag,currentPage));
        }
    },[currentPage])
    const { bannerList, recommendList, hasReadData, chapterCount, detailData,recommendListCount } = useSelector((state) => {
        return {
            bannerList: state.recommend.bannerList,
            recommendList: state.recommend.recommendList,
            recommendListCount:state.recommend.recommendListCount,
            hasReadData: state.detail.hasReadData,
            chapterCount: state.detail.chapterCount,
        }
    })
    const bannerListJS = bannerList ? bannerList : [];
    const recommendListJS = recommendList ? recommendList : [];


    const jump = useNavigate();
    const enterComicDetail = (id) => {
        jump(`/detail/${id}`);
    }

    const enterChapter = (id) => {
        jump(`/content/${id}`);
    }

    const chapterId = hasReadData.length !== 0 ? hasReadData[2][chapterCount].chapterId : null;
    const chapterName = hasReadData.length !== 0 ? hasReadData[2][chapterCount].title : null;

    return (
        <div>
            {
                (!isEmptyObject(bannerListJS) || !isEmptyObject(recommendListJS)) ? (
                    <div>
                        {/* <Slider bannerList={bannerListJS} ></Slider> */}
                        <HomeContentStyle>
                            {
                                (hasReadData.length !== 0) ? (
                                <LastWatch>
                                    <span>
                                        <span className="hasReadName" onClick={() => { enterComicDetail(hasReadData[1]) }}>{hasReadData[0]}</span>
                                        <span className="hasReadChapter" onClick={() => { enterChapter(chapterId) }}>{chapterName}</span>
                                    </span>
                                </LastWatch>) : null
                            }

                            <Tags onSelect={setTag}></Tags>
                            <ComicList recommendList={recommendListJS} currentTag={tag}></ComicList>
                            <Pagination defaultCurrent={1} onChange={(e)=>{ setCurrentPage(e)}} total={recommendListCount} defaultPageSize={12} showSizeChanger={false} style={{textAlign:"center",margin:5}}/>
                        </HomeContentStyle>


                    </div>
                ) : (
                    <Warn>
                        <div className="warn"></div>
                    </Warn>
                )
            }
        </div>

    )
}
export default React.memo(HomeContent);