import * as actionTypes from './constants';

const defaultState = {
    contentData: [],
    enterLoading: true,
}
export default (state = defaultState, action) => {
    switch (action.type) {
        case actionTypes.CHANGE_COMIC_CONTENT:
            return { ...state, ...{ contentData: action.data } };
        case actionTypes.CHANGE_COMIC_CONTENT_LOADING:
            return { ...state, ...{ enterLoading: action.data } };
        default:
            return state;
    }
}
