import React from "react";
import styled, { keyframes, keyFrames } from "styled-components";

const loading = keyframes`
    0%,100%{
        transform: scale(0.5);
    }
    50%{
        transform: scale(1.0);
    }
`
const LoadingWrapper = styled.div`
    >div{
        position: fixed;
        z-index: 1000;
        left: 0; 
        right: 0;  
        top: 0;
        bottom: 0;
        margin: auto;
        width: 100px;
        height: 100px;
        background-color: gray;
        opacity: 0.5;
        border-radius: 50%;
        animation: ${loading} 1.4s infinite ease-in;
    }
`
function Loading() {
    return (
        <LoadingWrapper>
            <div></div>
            <div></div>
        </LoadingWrapper>
    )
}
export default Loading;