import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAnySearchDataDispatch } from './store/actionCreator'
import SearchList from "../../component/searchList";
import { isEmptyObject } from "../../api/util";
import { SearchTab } from "./style";
import Loading from "../../ui/loading/loading";
import { Button,Input } from "antd";
import { SearchOutlined } from '@ant-design/icons';
import {Spin} from 'antd';
function ComicSearch(props) {
    const { searchData, searchPage, enterLoading } = useSelector((state) => {
        return {
            searchData: state.search.searchData,
            searchPage: state.search.searchPage,
            enterLoading: state.search.enterLoading
        }
    })
    const searchDataRel = searchData ? searchData : [];
    const dispatch = useDispatch();
    const searchTitle = (searchType, keyword, currentPage) => {
        const fixedQuery = keyword.replace(/[`:_.~!@#$%^&*() \+ =<>?"{}|, \/ ;' \\ [ \] ~@#%&* \+ ={}|]/g,
            '');
        console.log(fixedQuery)
        if (fixedQuery === '') {
            alert('')
        } else {
            dispatch(getAnySearchDataDispatch(searchType, fixedQuery, currentPage));
        }
    }
    const searchAuthor = (searchType, keyword, currentPage) => {
        const fixedQuery = keyword.replace(/[`:_.~!@#$%^&*() \+ =<>?"{}|, \/ ;' \\ [ \] ~@#%&* \+ ={}|]/g,
            '');
        console.log(fixedQuery)
        if (fixedQuery === '') {
            alert('')
        } else {
            dispatch(getAnySearchDataDispatch(searchType, fixedQuery, currentPage));
        }
    }

    const [query, setQuery] = useState('');
    const handleChange = (e) => {
        setQuery(e.currentTarget.value)
    };
    return (
        <div>
            <SearchTab>
                <Input className="input" type='text' value={query} placeholder='' onChange={handleChange} />

                <Button type='primary' style={{borderRadius:5}} className="searchBtn" onClick={() => { searchTitle('title', query, 1) }} icon={<SearchOutlined />}></Button>
                <Button type='primary' style={{borderRadius:5}} className="searchBtn" onClick={() => { searchAuthor('author', query, 1) }} icon={<SearchOutlined />}></Button>
            </SearchTab>
            {
                // enterLoading ? <Loading></Loading> : null
                enterLoading ? <Spin size="large" style={{marginLeft:800,marginTop:10}}/> : null
            }
            {
                (!isEmptyObject(searchDataRel) || searchDataRel != null) ? (
                    <SearchList searchData={searchDataRel} ></SearchList>
                ) : (<h1></h1>)
            }

        </div>
    )
}
export default ComicSearch;