import { axiosInstance, axiosInstance2 } from "./config";

export const getKuaikan = () => {
  return axiosInstance2.get('/v2/pweb/home');
}

export const getBannerRequest = (type) => {
  return axiosInstance.get(`/comic/search/comicType/${type}/1/10`);
}

export const getRecommendListRequest = (type,currentPage) => {
  return axiosInstance.get(`/comic/search/comicType/${type}/${currentPage}/12`);
}

export const getDetailDataRequest = (comicId) => {
  return axiosInstance.get(`/comicChapter/search/${comicId}`)
}

export const getComicContentRequest = (id) => {
  return axiosInstance.get(`/comicContent/search/${id}`)
}

export const getAnySearchRequest = (searchType, keyword, currentPage) => {
  return axiosInstance.get(`/comic/search/${searchType}/${keyword}/${currentPage}/10`)
}

export const getTypeComic = (keyword,currentPage)=>{
  return axiosInstance.get(`/comic/search/comicType/${keyword}/${currentPage}/10`)
}