import { getComicContentRequest } from '../../../api/request';
import { CHANGE_COMIC_CONTENT, CHANGE_COMIC_CONTENT_LOADING } from './constants';
const changeComicContent = (data) => ({
    type: CHANGE_COMIC_CONTENT,
    data
})
const changeContentLoading = (data)=>({
    type:CHANGE_COMIC_CONTENT_LOADING,
    data
})
export const getComicContent = (id) => {
    return dispatch => {
        dispatch(changeContentLoading(true));
        getComicContentRequest(id).then(res => {
            dispatch(changeComicContent(res.data));
            dispatch(changeContentLoading(false));
        }).catch(() => {
            console.log('')
        })
    }
}