import * as actionTypes from './constants';

const defaultState = {
  bannerList: [],
  recommendList: [],
  kuaiKanData: [],
  recommendListCount: 0
};

export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.CHANGE_BANNER:
      return { ...state, ...{ bannerList: action.data } };
    case actionTypes.CHANGE_RECOMMEND_LIST:
      return { ...state, ...{ recommendList: action.data } };
    case actionTypes.CHANGE_KUAI_KAN_DATA:
      return { ...state, ...{ kuaiKanData: action.data } };
    case actionTypes.CHANGE_RECOMMEND_LIST_COUNT:
      return { ...state, ...{ recommendListCount: action.data } };
    default:
      return state;
  }
}